import React, { useEffect, useState } from 'react';
import { useNavigate } from "react-router-dom";
import '../../styles/Project/ProjectDelModal.css';

const ProjectDelModal = ({ title, id, onClose, onConfirm }) => {
  const navigate = useNavigate();
  const [ip, setIp] = useState("");

  useEffect(() => {
    fetch('/cfg/ip.cfg')
      .then(res => res.json())
      .then(data => setIp(data.pc))
      .catch(err => console.error('ip.cfg  :', err));
  }, []);

  // ---------- API REQUEST ----------
  const handleConfirm = async () => {
    try {
      const response = await fetch(`${ip}/project/${id}`, {
        method: "DELETE",
      });
      if (!response.ok) throw new Error("  ");

      const data = await response.json();
      console.log("  :", data);

      if (onConfirm) onConfirm(id);
      navigate("/", { state: { refresh: true } });

    } catch (error) {
      console.error(" :", error);
      alert("   .");
    }
  };

  return (
    <div className="delete-overlay" onClick={onClose}>
      <div className="delete-box" onClick={(e) => e.stopPropagation()}>
        <h3>"{title}"<br />  ?</h3>
        <div className="btn-group">
          <button className="cancel-btn" onClick={onClose}></button>
          <button className="confirm-btn" onClick={handleConfirm}></button>
        </div>
      </div>
    </div>
  );
};

export default ProjectDelModal;